<template>
    <div class="carousel-3d-controls">
        <a href="#" class="prev" @click.prevent="parent.goPrev()"
           :class="{ disabled: !parent.isPrevPossible }">
            <span>&lsaquo;</span>
        </a>
        <ul class="carousel-nav">
            <li v-for="i in parent.getSlideCount()" :class="parent.currentIndex+1==i?'active':''">{{parent.currentIndex}} - {{i}}</li>
        </ul>
        <a href="#" class="next" @click.prevent="parent.goNext()"
           :class="{ disabled: !parent.isNextPossible }">
            <span>&rsaquo;</span>
        </a>
    </div>
</template>

<script>
    import {pub} from './pub.js'
    export default {
        name: 'controls',
        props: {
            index: {
                type: Number
            }
        },
        data () {
            return {
                parent: this.$parent
            }
        },
        created(){
            pub.$on('test',(msg) =>{
            })
        },
        methods:{
            controlGoto(){
            }
        }
    }
</script>

<style scoped lang="css">
    .carousel-3d-controls {
        position: absolute;
        z-index: 9099;
        right:80px;
        display: table-cell;
    }

    .next, .prev {
        display:block;
        width: 30px;
        z-index: 9999;
        font-size: 60px;
        height: 30px;
        line-height: 60px;
        color: #333;
        user-select: none;
        text-decoration: none;
    }

    .next span, .prev span {
        display:block;
        height:23px;
        width:23px;
        overflow:hidden;
        margin:0;
        border: solid #fff;
        border-width: 1px 0 0 1px;
        line-height: 140px;
        
    }
    .carousel-nav{
        padding:50px 0;
    }
    .carousel-nav li{
        width:6px;
        height:6px;
        background:#fff;
        overflow:hidden;
        border-radius:3px;
        margin: 4px auto;
        opacity:0.3;
    }
    .carousel-nav li.active{
        opacity:1;
    }

    .next:hover, .prev:hover {
        cursor: pointer;
        opacity: 0.7;
    }

    .prev span{
        transform:rotate(45deg) translateX(4px);
    }

    .next span{
        transform:rotate(-135deg) translateX(-4px);
    }

    .disabled {
        opacity: 0.2;
        cursor: default;
    }

    .disabled:hover {
        cursor: default;
        opacity: 0.2;
    }
</style>



