<template>
  <layout :active="3">
    <div class="protfolio">
      <div class="yearlist">
        <h3><em>作品</em>日期</h3>
        <div class="menus">
          <div v-for="ydata in yearsRoute" :class="yaerActive == ydata.year? 'active' : '' ">
            <router-link class="hover" :to="ydata.path">{{ ydata.year }}</router-link>
          </div>
        </div>
      </div>
      <div v-for="(item, index) in currentdt">
        <slides v-if="yaerActive === index" :database="item" :displays="displays" :startIndex="startIndex"></slides>
      </div>
      <div class="search">
        <input type="text"  placeholder="搜索" name="">
        <button class="fa fa-search"></button>
      </div>
    </div>
  </layout>
</template>

<script>
  import Layout from '../modules/layout.vue'
  import Slides from '../modules/slider';
  import {portfolio} from '../mock-data/portfolio.js'
  export default {
    components:{
      Layout,
      Slides
    },
		data(){
			return{
				currentdt:portfolio,
				yearsRoute:[],  // 年份路由
        years:[],       // 年份
				yaerActive:"",
				startIndex:1,
				displays:7
			}
		},
		beforeRouteUpdate (to, from, next) {
      this.initdata(to.params.year);
      next();
    },
    beforeRouteLeave (to, from, next) {
      let year = to.params.year
      if(!to.params.year&&from.params.year){
        year = this.years[this.years.length-1] || ''
      }
      this.initdata(year);
      next()
    },
		methods:{
			initdata(years){
	      var year = years || this.$route.params.year;
        if(!year) year = this.years[this.years.length-1] || '';
	      if(year&&this.currentdt&&this.currentdt[year]){
	      	this.yaerActive = year;
	      	this.displays = 7;
	      }
			},
      initYears(){
        this.yearsRoute = [];
        // 判断年份存在？不存在获取数据中最新的年份
        for(var a in portfolio) {
          this.yearsRoute.push({
            'year':a,
            'path':'/portfolio/'+a+'/review'
          })
          this.years.push(a);
        }
        this.yearsRoute = this.yearsRoute.reverse();  
      }
		},
    created(){
      this.initYears();
    	this.initdata();
    }

  }
</script>

<style lang="less" scoped>
.search{
	position:absolute;
	right:50px;
	top:20px;
	input{
		border:0;
		border-bottom:2px solid #fff;
		background:none;
		line-height:30px;
		padding:0 5px;
		outline:none;
		color:#fff;
		float: left;
	}
	button{
		background:none;
		color:#fff;
		border:0;
		outline:none;
		font-size:18px;
		padding:5px;
		cursor:pointer;
		margin-top:5px;
	}
}
.yearlist{
  position: absolute;
  z-index: 9099;
  right: 70px;
  display: block;
  top :0;
  bottom :0;
  margin: auto;
	height: 281px;
  background: #000;
  /*定义滚动条宽高及背景，宽高分别对应横竖滚动条的尺寸*/
  .menus::-webkit-scrollbar{
    width: 5px;
    // height: 16px;
    background-color: #f5f5f5;
  }
  /*定义滚动条的轨道，内阴影及圆角*/
  .menus::-webkit-scrollbar-track{
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    // border-radius: 5px;
    background-color: #000;
  }
  /*定义滑块，内阴影及圆角*/
  .menus::-webkit-scrollbar-thumb{
    /*width: 10px;*/
    height: 20px;
    border-radius: 5px;
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #fff;
  }

  .menus{
	  height: 228px;
	  overflow: hidden;
	  overflow-y:auto;
  }
	h3{
		color:#fff;
		font-size:14px;
		line-height:40px;
		em{
			border-bottom:2px solid #fff;
  			display:inline-block;
		}
	}
	.active{
		a{color:#fff;}
	}
	div{
		color:#666666;
		padding-top:15px;
		a{
			color:#676767;
		}
	}
	
} 
</style>


