<template>
  <div class="product">
    <!-- <modal v-if="showModal" @close="showModal = false"></modal> -->
    <div :class="showModal?'fullClose':''" @click="closeFullModal">
      <i class="icon-close"></i>
    </div>
    <div :class="showModal?'modal-mask':''">
      <div class="modal-wrapper">
      <carousel3d :controls-visible="true" :style="fullstyle" :border="borderWidth" :display="display">
        <slide v-for="(slide, i) in database" :index="i" :style="fullstyle">
          <img :src="database[i].img">
          <div class="mask">
            <div class="bg"></div>
            <i class="fa fa-expand" @click="fullModal"></i>
          </div>
        </slide>
      </carousel3d>
      </div>
    </div>
  </div>
</template>
<script>
  import { Carousel3d, Slide } from '../../slider/index';
  import LayoutMember from '../modules/layout_member.vue'
  import {userdata} from '../mock-data/userdata.js'
  export default {
    components:{
      LayoutMember,
      'carousel3d': Carousel3d,
      'slide': Slide,
    },
    data(){
      return{
        slides: [],
        userdatas:userdata,
        showModal: false,
        display:this.displays||7,
        borderWidth:1,
        fullstyle:{
          backgroundColor:"#000"
        }
      }
    },
    props:['database','displays'],
    created(){
      
    },
    computed:{
      // fullstyle(){
      //   return this.
      // }
    },
    methods:{
      fullModal(){
        this.display = 1;
        this.showModal = true;
        this.borderWidth = 0;
        this.fullstyle['height'] = (document.body.clientHeight -100) + 'px';
        this.fullstyle['width'] = (document.body.clientWidth -100) + 'px !important';
        this.fullstyle['marginTop'] = 0 + 'px';
      },
      closeFullModal(){
        this.display = 7;
        this.showModal = false;
        this.borderWidth = 1;
        this.fullstyle = {
          backgroundColor:"#000"
        };
      }
    }
  }
</script>

<style lang="less">
  .modal-wrapper{
    position: absolute;
    top: 0;
    bottom: 0;
    min-width: 870px;
    margin: auto;
    .carousel-3d-slider{
        width: 370px;
    }
    .carousel-3d-container{
      padding-right: 70px !important;
      position: absolute !important;
      margin: auto;
      top: 0;
      bottom: 0;
    }
  }

  .product{
    color: #fff;
    width: 100%;
    height: 100%;
    .icon-close{
      display:none;
    }
    .fullClose{
      position:fixed;
      z-index:9999;
      top:20px;
      right:20px;
      width:60px;
      height:60px;
      .icon-close{
        display:block;
        z-index:9999;
        width:60px;
        height:60px;
        cursor:pointer;
        &:before{
          width:60px;
        }
        &:after{
          height:60px;
        }
      }
    }
    // 预览样式
    .modal-mask{
      position: fixed;
      z-index: 9998;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 1);
      display: table;
      transition: opacity .3s ease;
      .mask{
        display:none;
      }
      .modal-wrapper {
        display: table-cell;
        vertical-align: middle;
        position: initial;
        .carousel-3d-container{
          padding-right: 0px !important;
          position: relative !important;
          margin: 20px auto;
          padding: 0;
          height: auto;
        }
      }
      .carousel-3d-controls{
        height: 60px;
        width: 100%;
        top: 45%;
        margin-top:0;
      }
      div.carousel-3d-slider{
        background:#000;
        width: inherit;
      }
      .prev,.next{
        position: absolute;
        height:60px;
        width:60px;
      }
      .prev{
        text-align: left;
        left: 50px;
      }
      .next{
        text-align: right;
        right: 50px;
      }
      .prev span,.next span{
        height:40px;
        width:40px;
        line-height:190px;
        color: #000;
        //transition: all 1s ease-in;
        transition: transform 800ms, opacity 800ms, visibility 800ms;

      }
      .prev span{
        transform: rotate(-45deg) translateX(4px);
      }
      .next span{
        transform: rotate(135deg) translateX(-4px);
        text-align: right;
      }
      .carousel-nav{
        display: none;
      }
    }
    .mask{
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      .bg,.fa-expand{
        transition: all .6s ease-in-out; 
      }
      .bg{
        position: absolute;
        background-color: rgba(0, 0, 0, 0.6);
        height: 100%;
        width: 100%;
        transition: all 1s ease-in-out;
      }
      .fa-expand{
        font-size: 33px;
        color:rgba(0, 0, 0, 0);
        top: 40%;
        position: relative;
      }
      &:hover{
        .bg{
          background-color: rgba(0, 0, 0, 0);
        }
        .fa-expand{
          color:rgba(255, 255, 255, 0.6);
          &:hover{
            color:rgba(255, 255, 255, 1);
            cursor: pointer;
          }
        }

      }
    }
  }
</style>




